import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      clipped: true,
      drawer: false,
      fixed: true,
      items: [
        {
          icon: "mdi-apps",
          title: "Dashboard",
          to: "/student",
        },
        {
          icon: "mdi-account-circle",
          title: "Profile",
          to: "/student/profile",
        },
        {
          icon: "mdi-google-classroom",
          title: "Sanclass",
          to: "/student/classes",
        },
        {
          icon: "mdi-calendar-text",
          title: "San Project",
          to: "/dashboard",
        },
        {
          icon: "mdi-calculator-variant",
          title: "San Hitung",
          to: "/dashboard",
        },
      ],
      miniVariant: false,
      title: "SANEDU",
      user: {
        photo: this.$auth.$state.user.profile_image,
        fullName: this.$auth.$state.user.name,
        email: this.$auth.$state.user.email,
      },
    };
  },
  mounted() {
    this.drawer = false;
  },
  computed: {
    ...mapState(["isAuth"]),
  },
  methods: {
    ...mapMutations(["SET_IS_AUTH", "SET_API_TOKEN"]),
    async logout() {
      await this.$auth.logout();
      this.SET_IS_AUTH(false);
      this.SET_API_TOKEN(null);

      this.$router.push("/");
    },
  },

  // components:{
  //   'app-sidebar': Sidebar,
  //   'app-header': Header,

  // }
};
