import { mapMutations } from 'vuex'
export default {
    methods:{

        ...mapMutations(['SET_IS_AUTH']),
      async  logout(){

         await this.$auth.logout();
         this.SET_IS_AUTH(false);
          this.$router.push('/login');
        }

    }
}
